import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import "../../CSS/shop.css";
import "../../CSS/shop_mobile.css";
import { LOAD_PRODUCT_REQUEST } from "../../reducers/adminProduct";
const Shop = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const goPage = (code) => {
    navigate(`/shopDetail/${code}`);
  };
  const { products } = useSelector((state) => state.adminProduct);
  const [uniqueProducts, setUniqueProducts] = useState([]);
  const [sortedProducts, setSortedProducts] = useState([]);
  const [selectedStyle, setSelectedStyle] = useState("");

  useEffect(() => {
    dispatch({
      type: LOAD_PRODUCT_REQUEST,
    });
  }, [dispatch]);
  useEffect(() => {
    const removeDuplicatesById = (lists) => {
      if (!lists || !Array.isArray(lists)) {
        return [];
      }
      const uniqueLists = [];
      const existingIds = [];

      for (const list of lists) {
        if (
          list &&
          list.product_code &&
          !existingIds.includes(list.product_code)
        ) {
          uniqueLists.push(list);
          existingIds.push(list.product_code);
        }
      }

      return uniqueLists;
    };

    setUniqueProducts(removeDuplicatesById(products));
  }, [products]);
  useEffect(() => {
    if (uniqueProducts.length > 0) {
      setSortedProducts(uniqueProducts);
    }
  }, [uniqueProducts]);
  console.log("uniqueProducts : ", uniqueProducts);
  const handleSort = (sort) => {
    let sortedProducts;
    switch (sort) {
      case "":
        // createdAt  (   )
        sortedProducts = [...uniqueProducts].sort(
          (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
        );
        break;

      case "":
        // product_name  ( )
        sortedProducts = [...uniqueProducts].sort((a, b) =>
          a.product_name.localeCompare(b.product_name)
        );
        break;

      case "":
        // product_salePrice   (    )
        sortedProducts = [...uniqueProducts].sort(
          (a, b) => a.product_salePrice - b.product_salePrice
        );
        break;

      case "":
        // product_salePrice   (    )
        sortedProducts = [...uniqueProducts].sort(
          (a, b) => b.product_salePrice - a.product_salePrice
        );
        break;

      default:
        //    
        sortedProducts = uniqueProducts;
        break;
    }
    setUniqueProducts(sortedProducts);
  };

  return (
    <div className="shop">
      <div className="shop_container">
        <p>Shop</p>
        <div className="nav">
          <p>{uniqueProducts && uniqueProducts.length}  .</p>
          <ul id="pc">
            <li
              onClick={() => {
                handleSort("");
                setSelectedStyle("");
              }}
              // style={{
              //   color: selectedStyle === "id" ? "#000035" : "inherit",
              // }}
            >
              
            </li>
            <li
              onClick={() => {
                handleSort("");
                setSelectedStyle("");
              }}
              // style={{
              //   color: selectedStyle === "name" ? "#000035" : "inherit",
              // }}
            >
              
            </li>
            <li
              onClick={() => {
                handleSort("");
                setSelectedStyle("");
              }}
              // style={{
              //   color:
              //     selectedStyle === "price" && sortOrder === "asc"
              //       ? "#000035"
              //       : "inherit",
              // }}
            >
              
            </li>
            <li
              onClick={() => {
                handleSort("");
                setSelectedStyle("");
              }}
              // style={{
              //   color:
              //     selectedStyle === "price" && sortOrder === "desc"
              //       ? "#000035"
              //       : "inherit",
              // }}
            >
              
            </li>
          </ul>
          {/* <select id="mobile" /> */}
        </div>
        <div className="article_container">
          {uniqueProducts &&
            uniqueProducts.map((product, index) => {
              return (
                <div
                  className="article"
                  key={index}
                  onClick={() => {
                    goPage(product.product_code);
                  }}
                >
                  <img
                    src={`/images/mainImage/${product.product_imgUrl}`}
                    alt=""
                  />
                  <p
                    className="product_title"
                    dangerouslySetInnerHTML={{ __html: product.product_name }}
                  ></p>
                  <p className="product_price">
                    {product.product_salePrice.toLocaleString()}
                  </p>
                </div>
              );
            })}
        </div>
        <div className="page_list">
          <p>{`< 1 >`}</p>
        </div>
      </div>
    </div>
  );
};

export default Shop;
