import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../../CSS/signin.css";
import "../../CSS/signin_mobile.css";
const SigninContent = () => {
  const navigate = useNavigate();
  const [checked, setChecked] = useState(true);
  const [checked1, setChecked1] = useState(true);
  const goPage = (path) => {
    navigate(path);
  };
  return (
    <div className="signin_s1">
      <div className="section_container">
        <div className="article_container">
          <div className="title_box">
            <img
              src="/images/login/back_btn.png"
              alt=""
              onClick={() => {
                navigate(-1);
              }}
            />
            <img src="/images/logo.png" alt="" />
          </div>
          <p>
            !
            <br />
             
            <br />
             .
          </p>
          <div className="check_box">
            {checked ? (
              <img
                src="/images/login/btn_checked.png"
                alt=""
                onClick={() => {
                  setChecked(!checked);
                }}
              />
            ) : (
              <img
                src="/images/login/btn_unchecked.png"
                alt=""
                onClick={() => {
                  setChecked(!checked);
                }}
              />
            )}

            <p>
                <span>(  )</span>
            </p>
          </div>
          <div className="accept_box">
            <div className="accept">
              {checked1 ? (
                <img
                  src="/images/signin/accept_btn.png"
                  alt=""
                  onClick={() => {
                    setChecked1(!checked1);
                  }}
                />
              ) : (
                ""
              )}

              <p>[]  14 .</p>
            </div>
            <div className="accept">
              <img src="/images/signin/accept_btn.png" alt="" />
              <p>
                []     <span></span>
              </p>
            </div>
            <div className="accept">
              <img src="/images/signin/accept_btn.png" alt="" />
              <p>[]    </p>
            </div>
            <div className="accept">
              <img src="/images/signin/accept_btn.png" alt="" />
              <p>
                []     <span></span>
              </p>
            </div>
          </div>
          <div
            className="submit_btn"
            onClick={() => {
              goPage("/signForm");
            }}
          >
             
          </div>
        </div>
      </div>
    </div>
  );
};

export default SigninContent;
