import axios from "axios";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Redirection = () => {
  const code = new URLSearchParams(window.location.search).get("code"); // URL    
  const navigate = useNavigate();

  useEffect(() => {
    let authProvider = "";

    const fetchUserInfo = async () => {
      try {
        let authCode = "";

        // URL   
        if (window.location.pathname === "/auth/kakao") {
          authProvider = "kakao";
          authCode = code;
        } else if (window.location.pathname === "/auth/naver") {
          authProvider = "naver";
          authCode = code;
        }

        if (!authCode) {
          throw new Error("Authorization code not found.");
        }

        //  API  
        const endpoint =
          authProvider === "kakao"
            ? `http://localhost:8282/api/auth/kakao?code=${authCode}`
            : `http://localhost:8282/api/auth/naver?code=${authCode}`;

        //   
        const response = await axios.get(endpoint);
        console.log(response.data);

        //       (  )
        localStorage.setItem("user", JSON.stringify(response.data));

        //      
        navigate("/login-success");
      } catch (error) {
        console.error(`${authProvider} login error:`, error);
        //    
      }
    };

    if (code) {
      fetchUserInfo();
    } else {
      console.error("Authorization code not found.");
    }
  }, [code, navigate]);

  return <div> .</div>;
};

export default Redirection;
